// single portfolio page
import { useEffect, useState } from "react";
import Lightbox from "react-image-lightbox";
import { useParams } from "react-router";
import { portfolios } from "../../data/data";

export default function PortfolioSingle() {
	const { index } = useParams();

	const [isOpen, setIsOpen] = useState(false);
	const image = [`/images/portfolios/${index}.jpg`];

	useEffect(() => {
		document.title = "Portfolio";
		window.scrollTo({ top: 0, behavior: "smooth" });
	}, []);

	return (
		<div className="animate__animated animate__fadeIn py-20">
			<div className="sm:flex sm:space-x-10">
				<div className="mb-10 sm:mb-0 sm:w-1/2 relative group">
					<div className="aspect-h-1 aspect-w-1">
						<img
							src={`/images/portfolios/${index}.jpg`}
							alt="portfolio"
							className="object-cover"
						/>
					</div>
					<button
						onClick={() => setIsOpen(true)}
						className="absolute top-5 right-5 flex items-center justify-center text-white focus:outline-none h-14 w-14 group-hover:opacity-70 opacity-0 transition-all duration-300 bg-black z-10">
						<svg
							xmlns="http://www.w3.org/2000/svg"
							className="h-6 w-6"
							fill="none"
							viewBox="0 0 24 24"
							stroke="currentColor">
							<path
								strokeLinecap="round"
								strokeLinejoin="round"
								strokeWidth={1}
								d="M4 8V4m0 0h4M4 4l5 5m11-1V4m0 0h-4m4 0l-5 5M4 16v4m0 0h4m-4 0l5-5m11 5l-5-5m5 5v-4m0 4h-4"
							/>
						</svg>
					</button>
				</div>
				<div className="sm:w-1/2">
					<h3 className="font-semibold mb-4 text-2xl text-primary">
						{portfolios[index - 1].title}
					</h3>
					<p className="mb-9">{portfolios[index - 1].description}</p>
					<a
						className="uppercase bg-primary tracking-wide text-sm text-white px-6 py-3"
						href={portfolios[index - 1].link}>
						visit work
					</a>
				</div>
			</div>
			{isOpen && (
				<Lightbox mainSrc={image[0]} onCloseRequest={() => setIsOpen(false)} />
			)}
		</div>
	);
}
